import * as running_config from "../config"
import axios from "axios"

const api_end_point = `${running_config.API_BASE}/storage`;

export interface IApplicationModel {
    id: string,
    title: string;
    summary: string;
    details: string;
    linkDescription: string;
}

export const DefaultApplicationModel: IApplicationModel = {
    id: "title",
    summary: "summary",
    title : "title",
    details: "details",
    linkDescription: "Go to AA →"
}

export const getData = async (id:string)  => await axios.get<IApplicationModel>(`${api_end_point}/applications/${id}`);

export const isExisting = async (id:string): Promise<boolean>  => {
    try {
        const response = await getData(id);
        return response.status === 200;
    }catch {
        return false;
    }
}

export const saveData: any = async (data: IApplicationModel) => {
   
    const postConfig = {
        method: 'post',
        url: `${api_end_point}/applications`,
    }

    const putConfig = {
        method: 'put',
        url: `${api_end_point}/applications/${data.id}`,
    }

    const isThisAnExistingApplication = await isExisting(data.id);
   
    const methodDirective = isThisAnExistingApplication ? putConfig : postConfig;
    
    const axiosConfig : any = {
        ...methodDirective,
        headers: {
            'Content-Type': 'application/json'
        },
        data : JSON.stringify({
            ...data,
            "type": "applications"
        })
    };

    await axios(axiosConfig);
}