import React from "react";
import { navigate, RouteComponentProps } from "@reach/router";
import { graphql } from "gatsby"

import {Card, CardContent, createStyles, makeStyles, Typography} from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import {useForm, usePlugin} from "tinacms";
import {InlineForm, InlineTextField} from 'react-tinacms-inline'
import { InlineWysiwyg } from 'react-tinacms-editor'

import { DefaultApplicationModel, getData, IApplicationModel, saveData } from "../cmsapi/ApplicationDetails.cms";
import useIsInDraftMode from "../hooks/useIsInDraftMode";
import Layout from "../components/Layout";
import ReactMarkdown from "react-markdown";

interface IRoutingProps extends RouteComponentProps {
    id?: string;
    pageContext?: any;
    data?: {
        allApplicationsJson: {
            nodes: IApplicationModel[]
        }
    }
}

const useStyles = makeStyles((_theme) =>
    createStyles({
        cardTitle: {
            fontFamily: "D-DIN",
            fontWeight: "bold",
            fontSize: "25px",
            paddingLeft: "16px",
            paddingTop: "8px",
            letterSpacing: "0.1px",
        },
        cardContent: {
            color: "#434343",
            fontSize: "16px",
            letterSpacing: "0.5px",
            lineHeight: "22px",
            paddingLeft: "16px",
        },
        blueText: {color: "#002776"},
        voxelCardRow: {
            marginBottom: 24,
        },
        voxelCardHeight: {
            height: "100%",
        },
        voxelCardShadow: {
            boxShadow:
                "0 2px 2px 0 rgba(0, 0, 0, 0.07), 0 1px 5px 0 rgba(0, 0, 0, 0.1)",
            mozBoxShadow:
                "0 2px 2px 0 rgba(0, 0, 0, 0.07), 0 1px 5px 0 rgba(0, 0, 0, 0.1",
        },
    })
);

export const query = graphql` query ($id: String!) {
      allApplicationsJson (filter: {id: {eq: $id}}) {
        nodes {
          details
          id
          linkDescription
          summary
          title
        }
      }
    }`;

const useFormConfig = (id: string, staticData: any): any => {
    
    const loadInitialValues = async () => {

        console.dir(staticData)

        if (useIsInDraftMode()) {
            try {
                const entry = await getData(id);
                return entry.data
            } catch (e) {
                return DefaultApplicationModel;
            }
        }else {
            return staticData?.allApplicationsJson.nodes[0]
        }
    }

    return {
        id: "applicationPage",
        label: 'Application Page',
        fields: [
            {
                name: 'title',
                label: 'Title',
                component: 'text',
                validate(title: any) {
                    if (!title) return "Required."
                }
            },
            {
                name: 'summary',
                label: 'Summary',
                component: 'text',
                validate(summary: any) {
                    if (!summary) return "Required."
                }
            },
            {
                name: 'details',
                label: 'Details',
                component: 'markdown',
                validate(details: any) {
                    if (!details) return "Required."
                }
            },
            {
                name: 'linkDescription',
                label: 'Link description',
                component: 'text',
                validate(linkDescription: any) {
                    if (!linkDescription) return "Required."
                }
            }
        ],
        loadInitialValues,
        onSubmit: async (formData: IApplicationModel) => {
            const payload = {
                ...formData,
                "id": id,
            }

            await saveData(payload)

            await navigate(`/`)
        },
    };
}

const useId = (routingProps: IRoutingProps): string => {
    let id = routingProps.id;
   
    if (routingProps.pageContext !== undefined) {
        id = routingProps.pageContext.id;
    }
    
    return id ?? "null";
}

const ApplicationDetails: React.FC<IRoutingProps> = (routingProps) => {

    const classes = useStyles();

    const id = useId(routingProps);
    
    const formConfig = useFormConfig(id, routingProps.data);

    const [model, form] = useForm(formConfig, {fields: formConfig.fields})

    usePlugin(form)

    return (
        <Layout>
            <Grid className={classes.voxelCardRow} container item>
                <Grid item md={12} sm={12} xs={12}>
                    <Card
                        id="applicationDetailsCard"
                        className={`${classes.voxelCardHeight} ${classes.voxelCardShadow}`}
                    >
                        <InlineForm form={form}>
                            <CardContent>
                                <Typography
                                    className={`${classes.cardTitle} ${classes.blueText}`}
                                    gutterBottom
                                    variant="h6"
                                    component="h6"
                                >
                                    <InlineTextField name="title" />
                                </Typography>
    
                                <Typography
                                    className={classes.cardContent}
                                    gutterBottom
                                    component="p"
                                >
                                    <InlineWysiwyg name="details" format="markdown">
                                        <ReactMarkdown source={model.details} />
                                    </InlineWysiwyg>
                                </Typography>                            
                            </CardContent>
                        </InlineForm>
                    </Card>
                </Grid>
            </Grid>
        </Layout>
    );
};


export default ApplicationDetails;